<script>
	// 	Note: Due to REPL limitations, full responsiveness may not work here. Download the example from here or from the website (https://layercake.graphics/example/Line) and run locally to get all features.
	
	import { LayerCake, Svg } from 'layercake';

	import Line from './Line.svelte';
	import Area from './Area.svelte';
	import AxisX from './AxisX.svelte';
	import AxisY from './AxisY.svelte';

	export let data;
	
	const formatTick = d => {
		let tick = d > 10000000 ? Math.round(d / 1000000) + 'm' : d > 10000 ? Math.round(d / 1000) + 'k' : d;
		return tick;
	};

	const xKey = 'x';
	const yKey = 'y';
	
	let xTicks = [];
	
	data.forEach(d => {
		xTicks.push(d[xKey]);
		d[yKey] = +d[yKey];
	});

</script>

<style>
	.chart-container {
		width: 100%;
		height: 200px;
		margin-top: 25px;
	}
</style>

<div class="chart-container">
	<LayerCake
		padding={{ right: 0, bottom: 20, left: 35 }}
		x={xKey}
		y={yKey}
		yDomain={[0, null]}
		data={data}
	>
		<Svg>
			<AxisX
				ticks={xTicks}
			/>
			<AxisY
				ticks={3}
				{formatTick}
			/>
			<Line/>
			<Area/>
		</Svg>
	</LayerCake>
</div>